import React from "react";
import Typewriting from "react-typewriting";


function Type() {
  return (
    <Typewriting
    strings={[
        'Computer Science Degree',
        'Full-stack Software Engineer',
    ]}
>
    {({ currentText, fullCurrentText }: any) => (
        <h1 className="titles" aria-label={fullCurrentText}>{currentText}</h1>
    )}
</Typewriting>
  );
}

export default Type;